import React,{Component} from 'react'
import { connect } from 'react-redux';
import MsgMe from '../components/msgMe'
import MsgToMe from '../components/msgToMe'
import ToolTip from '../components/toolTip'
import '../css/iphone.css'
class Iphone extends Component {
    constructor(props) {
        super(props)
        this.state = {
            isToolShow:true,
            fromDanHuang:[
                {   id:'000',
                    left:'',
                    response:[
                        {
                            content:'',
                            nextLeft:'001'
                        },{
                            content:'',
                            nextLeft:'002'
                        }
                    ]
                },
                {   id:'001',
                    left:'',
                    response:[
                        {
                            content:'',
                            nextLeft:'003'
                        },{
                            content:'',
                            nextLeft:'004'
                        },{
                            content:'',
                            nextLeft:'005'
                        }
                    ]
                },
                {   id:'002',
                    left:'',
                    response:[
                        {
                            content:'',
                            nextLeft:'006'
                        },{
                            content:'',
                            nextLeft:'007'
                        }
                    ]
                },
                {   id:'003',
                    left:'""',
                    response:[
                        {
                            content:'',
                            nextLeft:'008'
                        }
                    ]
                },
                {   id:'004',
                    left:'',
                    response:[
                        {
                            content:'',
                            nextLeft:'100'
                        }
                    ]
                },
                {   id:'005',
                    left:'',
                    response:[
                        {
                            content:'',
                            nextLeft:'100'
                        }
                    ]
                },
                {   id:'006',
                    left:'',
                    response:[
                        {
                            content:'',
                            nextLeft:'100'
                        }
                    ]
                },
                {   id:'007',
                    left:'...p',
                    response:[
                        {
                            content:'',
                            nextLeft:'009'
                        }
                    ]
                },
                {   id:'008',
                    left:'',
                    response:[
                        {
                            content:'',
                            nextLeft:'100'
                        }
                    ]
                },
                {   id:'009',
                    left:'p',
                    response:[
                        {
                            content:'',
                            nextLeft:'010'
                        }
                    ]
                },
                {   id:'010',
                    left:'',
                    response:[
                        {
                            content:'',
                            nextLeft:''
                        }
                    ]
                },
                {   id:'100',
                    left:'',
                    response:[
                        {
                            content:'',
                            nextLeft:'100',
                        }
                    ]
                },
            ],
            msg:[{left:'',right:''}],
            tooltip:''

        };


    }
    handleTool=()=>{
        this.setState(preState=>({
            isToolShow:!preState.isToolShow
        }));

    }
    handleNext=(e)=>{
        e.stopPropagation();
        e.preventDefault();
        let next=e.currentTarget.getAttribute('data-key');
        let value=e.currentTarget.getAttribute('value');
        let arr=this.state.msg;
        arr[arr.length-1].right=value;
        this.state.fromDanHuang.forEach(item=>{
            if(item.id==next){
                arr.push({left:item.left,right:''});
                this.setState(preState=>({
                    msg:arr,
                    tooltip:item.response,
                    isToolShow:!preState.isToolShow
                }));

            }
        })

    }

    componentDidMount() {
        this.setState(preState=>({
            tooltip:preState.fromDanHuang[0].response,

        }))
    }
    render(){
        return(
            <div className="iphone">
                <header>-</header>
                <div className="content">
                    {
                        this.state.msg.map((item,index) =>
                                <div key={'msgToMe'+index}>
                                    <MsgMe num={this.state.msg.length-1}index={index}msg={item.left}/>
                                    <MsgToMe msg={item.right}/>
                                </div>
                        )
                    }
                </div>
                <footer onClick={this.handleTool}>...</footer>
                {this.state.isToolShow?'':(
                    <ToolTip nextMsg={this.handleNext}
                             tooltip={this.state.tooltip}
                             onToolTip={this.handleTool}/>
                )}
            </div>
        )
    }
}
export default Iphone


